import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { DialogService } from 'app/shared/services/dialogs/dialog.service';
import { CorporateOnboardingService } from '../../onboarding-corporate/corporate-onboarding.service';
@Component({
  selector: 'app-success-dialogue',
  templateUrl: './success-dialogue.component.html',
  styleUrls: ['./success-dialogue.component.scss']
})
export class SuccessDialogueComponent implements OnInit {

  customerId : string;
  corporateId: {};
  hideCustId: boolean=false;
  hideCorporateId: boolean=false;

  constructor(
    public dialogRef: MatDialogRef<SuccessDialogueComponent>,
    private dialogService: DialogService,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private router: Router,
    // private corporateService: CorporateOnboardingService,
    private ls: LocalStoreService,
  ) { }

  ngOnInit(): void {
    console.log('data :: ',this.data);
   
    if(this.data.screenName === 'INDIVIDUAL'){
      this.hideCustId=true;
      this.hideCorporateId=false;
    }else if(this.data.screenName === 'CORPORATE'){
      this.hideCustId=false;
      this.hideCorporateId=true;
      this.corporateId=this.data.id;
    }
   this.customerId = this.ls.getItem('customerId');
  }

  closeClick() {
    this.dialogRef.close();
  }

  backToDashboard() {
    this.dialogRef.close();
    if(this.data.screenName === 'INDIVIDUAL'){
    // this.router.navigateByUrl('/others/mainNavigation');
       this.router.navigateByUrl(`/others/mainNavigation?customerId=${this.customerId}`);
    }else if(this.data.screenName === 'CORPORATE'){
      this.router.navigateByUrl(`/others/mainNavigation?customerId=${this.customerId}`);
    }
  }
  continue() {

    this.dialogRef.close();
    this.dialogService.openCustomerType();
    if(this.data.screenName === 'INDIVIDUAL'){
      console.log("testsssssssssssss");
      this.router.navigateByUrl('/others/newCustomerOnboarding');
    }else if(this.data.screenName === 'CORPORATE'){
      this.router.navigateByUrl('/onboardingCorporate/corporate');
    } 
  
  }


}
